'use client';

import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { Widget, WidgetType } from '@/types';

interface WidgetCardProps {
  widget: Widget;
  getWidgetIcon: (type: WidgetType) => string;
}

export function WidgetCard({ widget, getWidgetIcon }: WidgetCardProps) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: widget.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`bg-white rounded-xl shadow-md p-6 ${
        isDragging ? 'shadow-lg border-2 border-blue-400' : 'hover:shadow-md'
      }`}
    >
      <div 
        {...attributes}
        {...listeners}
        className="flex items-center mb-4 cursor-move"
      >
        <span className="text-2xl mr-3">{getWidgetIcon(widget.type)}</span>
        <h3 className="text-xl font-semibold text-gray-800">{widget.title}</h3>
      </div>
      
      <div className="h-40 bg-gray-100 rounded-lg flex items-center justify-center text-gray-400">
        {widget.type} chart/data will appear here
      </div>
      
      <div className="mt-4 flex justify-end">
        <button className="text-sm text-blue-600 hover:text-blue-800">
          View Details
        </button>
      </div>
    </div>
  );
}