'use client';

import { useState } from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragOverlay,
  type DragStartEvent,
  type DragEndEvent,
} from '@dnd-kit/core';
import {
  restrictToVerticalAxis, // Now imported from utilities
} from '@dnd-kit/modifiers'; // Yes, despite earlier message, it's back here
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { WidgetCard } from '@/components/WidgetCard';
import { Widget, WidgetType } from '@/types';

export default function DashboardPage() {
  const [widgets, setWidgets] = useState<Widget[]>([
    { id: '1', type: 'spending', title: 'Spending Overview' },
    { id: '2', type: 'budget', title: 'Budget Tracker' },
    { id: '3', type: 'net-worth', title: 'Net Worth' },
    { id: '4', type: 'transactions', title: 'Recent Transactions' },
    { id: '5', type: 'savings', title: 'Savings Progress' },
    { id: '6', type: 'goals', title: 'Financial Goals' },
  ]);

  const [activeWidget, setActiveWidget] = useState<Widget | null>(null);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragStart = (event: DragStartEvent) => {
    const { active } = event;
    const widget = widgets.find(w => w.id === active.id);
    if (widget) setActiveWidget(widget);
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    setActiveWidget(null);

    if (over && active.id !== over.id) {
      setWidgets((items) => {
        const oldIndex = items.findIndex(item => item.id === active.id);
        const newIndex = items.findIndex(item => item.id === over.id);
        return arrayMove(items, oldIndex, newIndex);
      });
    }
  };

  const getWidgetIcon = (type: WidgetType) => {
    switch (type) {
      case 'spending': return 'üí∞';
      case 'budget': return 'üìä';
      case 'net-worth': return 'üè¶';
      case 'transactions': return 'üßæ';
      case 'savings': return 'ü™ô';
      case 'goals': return 'üéØ';
      default: return 'üìå';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <h1 className="text-3xl font-bold text-gray-800 mb-8">Financial Dashboard</h1>
      
      <DndContext
        sensors={sensors}
        collisionDetection={closestCenter}
        modifiers={[restrictToVerticalAxis]}
        onDragStart={handleDragStart}
        onDragEnd={handleDragEnd}
      >
        <SortableContext 
          items={widgets}
          strategy={verticalListSortingStrategy}
        >
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {widgets.map((widget) => (
              <WidgetCard 
                key={widget.id}
                widget={widget}
                getWidgetIcon={getWidgetIcon}
              />
            ))}
          </div>
        </SortableContext>

        <DragOverlay>
          {activeWidget ? (
            <div className="bg-white rounded-xl shadow-lg p-6 border-2 border-blue-400">
              <div className="flex items-center mb-4">
                <span className="text-2xl mr-3">{getWidgetIcon(activeWidget.type)}</span>
                <h3 className="text-xl font-semibold text-gray-800">{activeWidget.title}</h3>
              </div>
              <div className="h-40 bg-gray-100 rounded-lg flex items-center justify-center text-gray-400">
                {activeWidget.type} chart/data will appear here
              </div>
            </div>
          ) : null}
        </DragOverlay>
      </DndContext>
    </div>
  );
}